import request from "@/helpers/request";
import friendlyDate from "@/helpers/util";

const URL = {
  GET: "/notes/trash",
  REVERT: "/notes/:noteItemId/revert",
  DELETE: "/notes/:noteItemId/confirm"
};

export default {
  getAll() {
    return new Promise((resolve, reject) => {
      request(URL.GET)
        .then(res => {
          res.data = res.data.sort((note1, note2) => {
            if (note1.updatedAt < note2.updatedAt) return 1;
            else return -1; //按照更新日期来排序
          });

          res.data.forEach(noteItem => {
            noteItem.friendlyCreatedAt = friendlyDate(noteItem.createdAt);
            noteItem.friendlyUpdatedAt = friendlyDate(noteItem.updatedAt);
          });

          resolve(res);
        })
        .catch(err => reject(err));
    });
  },

  delete({ noteItemId }) {
    return request(URL.DELETE.replace(":noteItemId", noteItemId), "DELETE");
  },

  revert({ noteItemId }) {
    return request(URL.REVERT.replace(":noteItemId", noteItemId), "PATCH");
  }
};
