import Trash from "@/apis/trash";
import { Message } from "element-ui";

const state = {
  trashNoteItems: null,
  curTrashNoteItemId: null
};

const getters = {
  trashNoteItems: state => state.trashNoteItems || [],

  curTrashNoteItem: (state, getters) => {
    if (!state.curTrashNoteItemId) return getters.trashNoteItems[0] || {};
    return (
      state.trashNoteItems.find(
        noteItem => noteItem.id == state.curTrashNoteItemId
      ) || {}
    );
  },

  belongTo: (state, getters, rootState, rootGetters) => {
    let note =
      rootGetters.noteList.find(
        note => note.id == getters.curTrashNoteItem.notebookId
      ) || {};

    return note.title || "";
  }
};

const mutations = {
  getAllTrashNoteItems(state, { trashNoteItems }) {
    state.trashNoteItems = trashNoteItems;
  },

  deleteTrashNoteItem(state, { noteItemId }) {
    state.trashNoteItems = state.trashNoteItems.filter(
      trashNoteItem => trashNoteItem.id != noteItemId
    );
  },

  setCurTrashNoteItem(state, payload = {}) {
    state.curTrashNoteItemId = payload.curTrashNoteItemId;
  } //初始化默认展示第一个，跳转时路由, 刷新时候
};

const actions = {
  getAllTrashNoteItems({ commit }) {
    return Trash.getAll().then(res => {
      commit("getAllTrashNoteItems", { trashNoteItems: res.data });
    });
  },

  deleteTrashNoteItem({ commit }, { noteItemId }) {
    return Trash.delete({ noteItemId }).then(res => {
      commit("deleteTrashNoteItem", { noteItemId });
      Message.success(res.msg);
    });
  },

  revertTrashNoteItem({ commit }, { noteItemId }) {
    return Trash.revert({ noteItemId }).then(res => {
      commit("deleteTrashNoteItem", { noteItemId });
      Message.success(res.msg);
    });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
