//笔记本增删改查的API封装

import request from "@/helpers/request";
import friendlyDate from "@/helpers/util";

const URL = {
  GET: "/notebooks",
  ADD: "/notebooks",
  EDIT: "/notebooks/noteId",
  DELETE: "/notebooks/noteId"
};

export default {
  getAll() {
    return new Promise((resolve, reject) => {
      request(URL.GET)
        .then(res => {
          res.data = res.data.sort((note1, note2) => {
            if (note1.updatedAt < note2.updatedAt) return 1;
            else return -1; //按照更新日期来排序
          });

          res.data.forEach(noteItem => {
            noteItem.friendlyCreatedAt = friendlyDate(noteItem.createdAt);
            noteItem.friendlyUpdatedAt = friendlyDate(noteItem.updatedAt);
          });

          resolve(res);
        })
        .catch(err => reject(err));
    });
  }, //获取笔记本列表

  addNote({ title = "" } = { title: "" }) {
    return request(URL.ADD, "POST", { title });
  }, //创建新笔记本

  editNoteName(noteId, { title = "" } = { title: "" }) {
    return request(URL.EDIT.replace("noteId", noteId), "PATCH", { title });
  }, //编辑笔记本标题

  deleteNote(noteId) {
    return request(URL.DELETE.replace("noteId", noteId), "DELETE");
  } //删除笔记本
};
