//笔记的接口封装
import request from "@/helpers/request";
import friendlyDate from "@/helpers/util";

const URL = {
  GET: "/notes/from/noteId", //从id为noteId的笔记本中获取笔记列表
  ADD: "/notes/to/noteId", //创建笔记到id为noteId的笔记本中
  EDIT: "/notes/noteItemId", //修改id为noteItemId的笔记
  DELETE: "/notes/noteItemId", //把id为noteItemId的笔记放进回收站
  DELETECONFIRM: "/notes/noteItemId/comfirm" //id为noteItemId的笔记从回收站彻底删除
};

export default {
  getAll({ noteId }) {
    return new Promise((resolve, reject) => {
      request(URL.GET.replace("noteId", noteId))
        .then(res => {
          //   res.data = res.data.sort((noteItem1, noteItem2) => {
          //     if (noteItem1.updatedAt < noteItem2.updatedAt) return 1;
          //     else return -1; //按照更新日期来排序
          //   });

          res.data.forEach(noteItem => {
            noteItem.friendlyCreatedAt = friendlyDate(noteItem.createdAt);
            noteItem.friendlyUpdatedAt = friendlyDate(noteItem.updatedAt);
          });

          resolve(res);
        })
        .catch(err => reject(err));
    });
  }, //获取该笔记本中笔记列表

  add({ noteId }, { title = "", content = "" } = { title: "", content: "" }) {
    return new Promise((resolve, reject) => {
      request(URL.ADD.replace("noteId", noteId), "POST", {
        title,
        content
      })
        .then(res => {
          res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt);
          res.data.friendlyUpdatedAt = friendlyDate(res.data.updatedAt);
          resolve(res);
        })
        .catch(err => reject(err));
    });
  }, //创建新笔记

  edit({ noteItemId }, { title, content }) {
    return request(URL.EDIT.replace("noteItemId", noteItemId), "PATCH", {
      title,
      content
    });
  }, //编辑笔记

  delete({ noteItemId }) {
    return request(URL.DELETE.replace("noteItemId", noteItemId), "DELETE");
  } //删除笔记到回收站
};
