//将response中的格式化的时间信息人性化
export default function friendlyDate(dateStr) {
  let dateObj = typeof dateStr === "object" ? dateStr : new Date(dateStr);
  let time = dateObj.getTime();
  let now = Date.now();
  let gap = now - time;
  let str = "";

  switch (true) {
    case gap < 1000 * 60:
      str = "刚刚";
      break;
    case gap < 1000 * 60 * 60:
      str = Math.floor(gap / (1000 * 60)) + "分钟前";
      break;
    case gap < 1000 * 60 * 60 * 24:
      str = Math.floor(gap / (1000 * 60 * 60)) + "小时前";
      break;
    default:
      str = Math.floor(gap / (1000 * 60 * 60 * 24)) + "天前";
  }

  return str;
}
