//对axios的进一步封装，主要体现在cookie，baseURL,validityState,还有状态码的细分

import axios from "axios";
import baseURLConfig from "./config-baseURL";
import { Message, Button } from "element-ui";

//默认配置
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";
axios.defaults.baseURL = baseURLConfig.baseURL;
axios.defaults.withCredentials = true; //默认发送cookie,服务端保存信息，避免刷新后要重新登录

export default function request(url, type = "GET", data = {}) {
  return new Promise((resolve, reject) => {
    //传入axios的配置项
    let option = {
      url,
      method: type,
      validityState(status) {
        return (satus >= 200 && status < 300) || status === 400;
        //validityState返回true的话即resolve promise,false 即 reject,根据前后端约定文档来写
      }
    };

    if (type.toLowerCase() === "get") {
      option.params = data; //get方法和post方法需要配置的属性不同，另加判断
    } else {
      option.data = data;
    }

    axios(option)
      .then(res => {
        if (res.status === 200) {
          resolve(res.data); //响应状态码为200,resolve
          console.log(res.data);
        } else {
          reject(res.data); //不是200，在200~300之间，reject抛出
        }
      })
      .catch(err => {
        Message({
          type: "error",
          message: err.response.data.msg
        });
        reject(err);
      });
    //axios请求失败，返回rejected的promise，跳catch（即状态码400）
    //接受axios中的then方法的reject抛出的响应信息
  });
}
