import noteItems from "@/apis/noteItem";
import { Message } from "element-ui";

const state = {
  noteItems: null, //笔记列表
  curNoteItemId: null
};

const getters = {
  noteItems: state => state.noteItems || [],

  curNoteItem: state => {
    if (!Array.isArray(state.noteItems)) return { title: "", content: "" };

    if (!state.curNoteItemId)
      //curNoteId不存在，默认curNoteItem为第一个笔记；两种情况，一种是我们从笔记本列表跳转至笔记详情，二是我们从下拉框选择不同笔记本。
      return state.noteItems[0] || { title: "", content: "" };

    return (
      state.noteItems.find(noteItem => noteItem.id == state.curNoteItemId) || {
        title: "",
        content: ""
      }
    );
  }
};

const mutations = {
  getNoteItems(state, payload) {
    state.noteItems = payload.noteItems;
  },
  addNoteItem(state, payload) {
    state.noteItems.unshift(payload.noteItem);
  },
  editNoteItem(state, payload) {
    let noteItem =
      state.noteItems.find(noteItem => noteItem.id == payload.noteItemId) || {};
    noteItem.title = payload.title;
    noteItem.content = payload.content;
    noteItem.friendlyUpdatedAt = "刚刚";
  },
  deleteNoteItem(state, payload) {
    state.noteItems = state.noteItems.filter(
      noteItem => noteItem.id != payload.noteItemId
    );
  },
  setCurNoteItem(state, payload = {}) {
    state.curNoteItemId = payload.curNoteItemId;
  }
};

const actions = {
  getNoteItems({ commit }, { noteId }) {
    return noteItems
      .getAll({ noteId })
      .then(res => commit("getNoteItems", { noteItems: res.data }));
  },
  addNoteItem({ commit }, { noteId }) {
    return noteItems.add({ noteId }).then(res => {
      commit("addNoteItem", { noteItem: res.data });
      Message.success(res.msg);
    });
  },
  editNoteItem({ commit }, { noteItemId, title, content }) {
    return noteItems.edit({ noteItemId }, { title, content }).then(() => {
      commit("editNoteItem", { noteItemId, title, content });
    });
  },
  deleteNoteItem({ commit }, { noteItemId }) {
    return noteItems.delete({ noteItemId }).then(res => {
      commit("deleteNoteItem", { noteItemId });
      Message.success(res.msg);
    });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
