import List from "@/apis/list";
import { Message } from "element-ui";

const state = {
  noteList: null,
  curNoteId: null
};

const getters = {
  noteList: state => state.noteList || [],

  curNote: state => {
    if (!Array.isArray(state.noteList)) return {}; //笔记列表为空的情况
    if (!state.curNoteId) return state.noteList[0] || {}; //我们直接点开笔记详情页面而不是从笔记列表页面跳转过来，此时路由上没有查询参数可以提供noteId,那我们默认显示列表第一个笔记本
    return state.noteList.find(note => note.id == state.curNoteId) || {}; //从列表页面跳转过来，路由上的noteId查询参数提供curNoteId 或者 点击下拉框，由command提供curNoteId
  }
};

const mutations = {
  getNoteList(state, payload) {
    state.noteList = payload.noteList;
  },
  createNote(state, payload) {
    state.noteList.unshift(payload.note);
    payload.note.friendlyUpdatedAt = "刚刚";
    payload.note.friendlyCreatedAt = "刚刚";
  },
  editNote(state, payload) {
    let note = state.noteList.find(note => note.id == payload.noteId) || {};
    note.title = payload.title;
    note.friendlyUpdatedAt = "刚刚";
  },
  deleteNote(state, payload) {
    state.noteList = state.noteList.filter(note => note.id != payload.noteId);
  },
  setCurNote(state, payload = {}) {
    state.curNoteId = payload.curNoteId;
  }
};

const actions = {
  getNoteList({ commit }) {
    if (state.noteList !== null) return Promise.resolve();

    return List.getAll().then(res =>
      commit("getNoteList", { noteList: res.data })
    );
  },
  createNote({ commit }, payload) {
    return List.addNote({ title: payload.title }).then(res => {
      commit("createNote", { note: res.data });
      Message.success(res.msg);
    });
  },
  editNote({ commit }, payload) {
    return List.editNoteName(payload.noteId, { title: payload.title }).then(
      res => {
        commit("editNote", { noteId: payload.noteId, title: payload.title });
        Message.success(res.msg);
      }
    );
  },
  deleteNote({ commit }, payload) {
    return List.deleteNote(payload.noteId).then(res => {
      commit("deleteNote", { noteId: payload.noteId });
      Message.success(res.msg);
    });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
